@mixin list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

@mixin mobile-container {
  box-sizing: border-box;
  width: $mobile-width;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}

@mixin tablet-container {
  width: $tablet-width;
  padding-right: 30px;
  padding-left: 30px;
}

@mixin desktop-container {
  width: $desktop-width;
  padding-right: 40px;
  padding-left: 40px;
}
